.card{ 
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid gray;
    border-radius: 10px  10px 0px 0px;
    overflow: hidden;
}

.card-browser {
    height: 20px;
    background-color: gray;
    display: flex;
    align-items: center;
}

.card-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: antiquewhite;
}

@media screen and (max-width: 500px) {
    .card-image{
        max-width: 250px;
        max-height: 250px;
    }
    
}